import { addResource, getResource, deleteResource } from "./apiServices";
class TokenService {
  #_apiBase = "http://185.185.70.166:5195/api/token";
  async getTokens() {
    return await getResource(`${this.#_apiBase}`);
  }
  async deleteToken(id) {
    return await deleteResource(`${this.#_apiBase}/${id}`);
  }
  async addToken(tarifName) {
    return await addResource(tarifName, `${this.#_apiBase}`);
  }
}
export default TokenService;
