import React, { Component } from "react";
import { Form, Button } from "react-bootstrap";
import Validator from "../../functions/validateFunctions";
class TarifModal extends Component {
  constructor(props) {
    super(props);
    let { nameT, duration, count } = props;
    this.state = {
      nameTarif: nameT ? nameT : "",
      durationTarif: duration ? duration : "",
      costTarif: count ? count : "",
      wrongParametres: false,
    };
  }
  onInput = (e) => {
    let value = e.target.value;
    if (e.target.getAttribute("data-content").indexOf("name") === -1) {
      if (!Validator.validateDigitInput(value)) {
        return;
      }
    } else {
      if (!Validator.validateNames(value)) return;
    }
    this.setState({ [e.target.getAttribute("data-content")]: e.target.value });
  };
  render() {
    let { nameTarif, durationTarif, costTarif } = this.state;
    return (
      <div
        onClick={(e) => {
          if (e.target.classList.contains("close")) this.props.onToggle();
        }}
        onKeyDown={(e) => {
          if (e.key === "Escape") this.props.onToggle();
        }}
        style={{ height: "100vh", backgroundColor: "rgba(0,0,0,.5)" }}
        className="close d-flex position-absolute  top-0 left-0 w-100 align-items-center justify-content-center"
      >
        <Form className="bg-light p-3 text-black border border-info">
          <Form.Group className="mb-3">
            <Form.Label> </Form.Label>
            <Form.Control
              onChange={this.onInput}
              type="text"
              className="fs-5"
              data-content="nameTarif"
              value={nameTarif}
              placeholder="  "
            />
          </Form.Group>

          <Form.Group className="mb-3">
            <Form.Label></Form.Label>
            <Form.Control
              onChange={this.onInput}
              type="text"
              data-content="durationTarif"
              className="fs-5"
              value={durationTarif}
              placeholder="  "
            />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label></Form.Label>
            <Form.Control
              onChange={this.onInput}
              type="text"
              data-content="costTarif"
              className="fs-5"
              value={costTarif}
              placeholder="  "
            />
          </Form.Group>
          <Button
            onClick={(e) => {
              e.preventDefault();
              this.props.onAction(nameTarif, durationTarif, costTarif);
            }}
            variant="primary"
            type="submit"
          >
            
          </Button>
        </Form>
      </div>
    );
  }
}
export default TarifModal;
