class Validator {
  static validateDigitInput = function validateInput(inputValue) {
    var regex = /^[0-9]*$/;
    return regex.test(inputValue);
  };
  static validateNames = function(name) {
    var regex = /^[a-zA-Z--0-9\s]*$/;
    return regex.test(name);
  };
}
export default Validator;
