import { Container, Col, Row } from "react-bootstrap";
import UserList from "./usersListComponent/usersList";
import { UserService } from "../../../services/apiServices";
import React, { Component } from "react";
import UserListItem from "./userListItem/userListItem";
import Search from "../../searchComponent/searchComponent";
import Error from "../../errorMessageComponent/errorMessageComponent";
import Label from "../../errorMessageComponent/notFound";
import DataModal from "../../DataModal/dataModal";
import { Button } from "react-bootstrap";
import Portal from "../../portalComponent/portalComponent";
import Load from "../../loadComponent/load";
class UserComponent extends Component {
  state = {
    loadingData: false,
    users: [],
    connectionError: false,
    errorMessage: "",
    keyWord: "",
    showDataModal: false,
  };
  #userService = new UserService();
  componentDidMount() {
    this.#userService
      .getUsers()
      .then((users) => this.setState({ users }))
      .catch((e) =>
        this.setState({ errorMessage: e.message, connectionError: true })
      );
  }
  #onUserDelete = (id) => {
    this.#userService
      .deleteUser(id)
      .then(() => {
        this.setState((state) => {
          return { users: state.users.filter((x) => x.idUser !== id) };
        });
      })
      .catch((e) =>
        this.setState({ errorMessage: e.message, connectionError: true })
      );
  };
  #onError = (e) => {
    this.setState({ connectionError: true, errorMessage: e });
  };
  #onToggleLoad = (e) => {
    this.setState((state) => ({ loadingData: !state.loadingData }));
  };
  #toglleDataModal = (e) => {
    this.setState((state) => ({ showDataModal: !state.showDataModal }));
  };
  #onSearchInput = (value) => {
    this.setState({ keyWord: value });
  };
  #filterUsersByKeyWordInUsername = () => {
    return this.state.users.filter(
      (x) =>
        x.username.toLowerCase().indexOf(this.state.keyWord.toLowerCase()) != -1
    );
  };
  componentDidCatch(error) {
    this.setState({ connectionError: true, errorMessage: error });
  }
  onSingleUserUpdatex;
  render() {
    let dataModal = this.state.showDataModal ? (
      <Portal>
        <DataModal
          onExit={this.#toglleDataModal}
          onLoad={this.#onToggleLoad}
          onError={this.#onError}
        ></DataModal>
      </Portal>
    ) : null;
    let potentialError = this.state.connectionError ? (
      <Error message={this.state.errorMessage}></Error>
    ) : null;
    let filteredUsers = this.#filterUsersByKeyWordInUsername().map((item) => {
      return <UserListItem {...item} key={item.idUser}></UserListItem>;
    });
    let loading = this.state.loadingData ? (
      <Load message=" ..."></Load>
    ) : null;
    let notification =
      filteredUsers.length === 0 ? (
        <Label message="  "></Label>
      ) : null;
    return (
      <div
        className="d-block mx-auto bg-white w-75 p-3 overflow-y-hidden"
        style={{ height: "100vh" }}
      >
        {dataModal}
        {potentialError}
        <Search onInput={this.#onSearchInput}></Search>
        <div className="d-flex p-3">
          <Button
            className="me-5"
            variant="warning"
            onClick={this.#toglleDataModal}
          >
             
          </Button>
          {loading}
        </div>
        <div className="d-flex justify-content-between bg-info p-3">
          <span className="w-9 text-white"></span>
          <span className="w-9 text-white">pay_usdt</span>
          <span className="w-9 text-white">pay_ton</span>
          <span className="w-9 text-white">pay_del</span>
          <span className="w-9 text-white">pay_rub</span>
          <span className="w-9 text-white"></span>
          <span className="w-9 text-white"></span>
          <span className="w-9 text-white"> </span>
          <span className="w-9 text-white">DPT</span>
          <span className="w-9 text-white">Registation</span>
        </div>
        {notification}
        <UserList>{filteredUsers}</UserList>
      </div>
    );
  }
}
export default UserComponent;
