import { getResource, updateResource } from "./apiServices";

class SettingService {
  #_apiBase = "http://185.185.70.166:5195/api/setting";
  async getSetting() {
    return await getResource(this.#_apiBase);
  }
  async updateSetting(updateSettingDto) {
    return await updateResource(updateSettingDto, `${this.#_apiBase}`);
  }
}
export default SettingService;
