import { getResource, DataTransformer } from "./apiServices";
class ReferalService {
  #_apiBase = `http://185.185.70.166:5195/api/referal`;
  async getReferals(username) {
    let users = await getResource(`${this.#_apiBase}/${username}`);
    return users.map((item) => DataTransformer.tranformUserInfo(item));
  }
}
export default ReferalService;
