import { useDebugValue } from "react";
let mustHaveHeader = {
  Authorization: `Bearer ${localStorage.getItem("token")}`,
};
function redefineToken() {
  mustHaveHeader["Authorization"] = `Bearer ${localStorage.getItem("token")}`;
}
export { redefineToken, mustHaveHeader };
async function addResource(DtoObjectToAdd, url) {
  let res = await fetch(url, {
    method: "POST",
    headers: Object.assign(mustHaveHeader, {
      "Content-type": "application/json",
    }),
    body: JSON.stringify(DtoObjectToAdd),
  });
  if (res.ok) {
    return await res.json();
  }
  throw new Error("   ");
}
async function getResource(url) {
  let res = await fetch(url, {
    headers: { ...mustHaveHeader },
  });
  if (res.ok && url.indexOf("referal") === -1) {
    return await res.json();
  } else {
    if (res.ok) {
      if (res.status !== 204) return await res.json();
      return [];
    }
  }
  throw new Error(`   ${res.status}`);
}
async function deleteResource(url) {
  let res = await fetch(url, {
    method: "DELETE",
    headers: { ...mustHaveHeader },
  });
  if (res.ok) return res;
  throw new Error(`Bad request ${res.status}`);
}
async function updateResource(DtoObjectToUpdate, url) {
  let res = await fetch(url, {
    method: "PUT",
    headers: {
      "Content-type": "application/json",
      ...mustHaveHeader,
    },
    body: JSON.stringify(DtoObjectToUpdate),
  });
  if (res.ok) return res;
  throw new Error(`   .   ${res.status}`);
}

class DataTransformer {
  static tranformUserInfo(obj) {
    return {
      idUser: obj.idUser,
      username: obj.username,
      payUsdt: obj.payUsdt,
      payDel: obj.payDel,
      payTon: obj.payTon,
      payRub: obj.payRub,
      isRef: obj.isRef,
      isActive: obj.isActive,
      statusTarif: obj.statusTarif,
      datePayTarif: obj.datePayTarif
        ? `${obj.datePayTarif.day}-${obj.datePayTarif.month}-${obj.datePayTarif.year}`
        : null,
      dateReg: obj.dateReg
        ? `${obj.dateReg.day}-${obj.dateReg.month}-${obj.dateReg.year}`
        : null,
    };
  }
}

class UserService {
  #_apiBase = "http://185.185.70.166:5195/api/user";
  async getUsers() {
    let users = await getResource(this.#_apiBase);
    return users.map((item) => DataTransformer.tranformUserInfo(item));
  }
  async getSingleUser(id) {
    return await getResource(`${this.#_apiBase}/${id}`);
  }
  async deleteUser(id) {
    return await deleteResource(`${this.#_apiBase}/${id}`);
  }
  async updateUser(UpdateUserDto, id) {
    console.log(UpdateUserDto, id);
    return await updateResource(UpdateUserDto, `${this.#_apiBase}/${id}`);
  }
}
export {
  UserService,
  deleteResource,
  getResource,
  updateResource,
  addResource,
  DataTransformer,
};
