import React, { Component } from "react";

class TokenListItem extends Component {
  render() {
    let { nameT, idToken, token, onDelete } = this.props;
    return (
      <li className="d-flex justify-content-between p-3 border border-info">
        <span style={{ width: "40%" }} className="text-center">
          {nameT}
        </span>
        <span style={{ width: "40%" }} className="text-center">
          {token}
        </span>
        <button
          onClick={(e) => onDelete(idToken)}
          className="delete border-0 text-danger fs-5"
        >
          <i class="bi bi-trash"></i>
        </button>
      </li>
    );
  }
}
export default TokenListItem;
