import { Component } from "react";
import TarifList from "./tarifList/tarifList";
import React from "react";
import TarifService from "../../../services/tarifService";
import Search from "../../searchComponent/searchComponent";
import TarifListItem from "./tarifListItem/tarifListItem";
import Error from "../../errorMessageComponent/errorMessageComponent";
import TarifModal from "../../tarifModal/tarifModal";
import Portal from "../../portalComponent/portalComponent";
import Label from "../../errorMessageComponent/notFound";
import { Button } from "react-bootstrap";
import TokenModal from "../../tokenModal/tokenModal";
class TarifSection extends Component {
  #tarifService = new TarifService();
  constructor(props) {
    super(props);
    this.state = {
      tarifs: [],
      error: false,
      errorMessage: "",
      keyword: "",
      modal: false,
      modalDestination: "add",
      selectedItem: null,
      showTokenModal: false,
    };
  }

  componentDidMount() {
    this.#tarifService
      .getTarifs()
      .then((e) => this.setState({ tarifs: e }))
      .catch((e) => this.setState({ error: true, errorMessage: e.message }));
  }

  onInput = (keyword) => {
    this.setState({ keyword });
  };
  #toggleTokenModal = (e) => {
    this.setState((state) => ({ showTokenModal: !state.showTokenModal }));
  };
  onFilter = () => {
    if (Array.isArray(this.state.tarifs))
      return this.state.tarifs.filter(
        (item) =>
          item.nameT.toLowerCase().indexOf(this.state.keyword.toLowerCase()) !=
          -1
      );
    else debugger;
  };

  onToggleModal = () => {
    this.setState((state) => ({ modal: !state.modal }));
  };

  #switchToAdd = () => this.setState({ modalDestination: "add" });

  #switchToUpdate = (id) =>
    this.setState({ modalDestination: "update", selectedItem: id });

  onAdd = (nameT, duration, count) => {
    this.#tarifService
      .addTarif({ nameT, duration, count })
      .then(({ idTarif }) => {
        this.setState((state) => {
          return {
            tarifs: [{ nameT, duration, count, idTarif }, ...state.tarifs],
          };
        });
      })
      .catch((e) => this.setState({ error: true, errorMessage: e.message }));
  };

  onUpdate = (nameT, duration, count) => {
    this.#tarifService
      .updateTarif({ nameT, duration, count }, this.state.selectedItem)
      .then(() => {
        this.setState((state) => {
          return {
            tarifs: state.tarifs.map((item) => {
              if (item.idTarif !== state.selectedItem) return item;
              return { nameT, duration, count, idTarif: item.idTarif };
            }),
          };
        });
      })
      .catch((e) => this.setState({ error: true, errorMessage: e.message }));
  };

  onDelete = (id) => {
    this.#tarifService
      .deleteTarif(id)
      .then(
        this.setState((state) => {
          return { tarifs: state.tarifs.filter((item) => item.idTarif !== id) };
        })
      )
      .catch((e) => this.setState({ error: true, errorMessage: e.message }));
  };

  render() {
    let tokenModal = this.state.showTokenModal ? (
      <Portal>
        <TokenModal
          onExit={this.#toggleTokenModal}
          tarifs={this.state.tarifs}
        ></TokenModal>
      </Portal>
    ) : null;
    let correct =
      this.state.modalDestination === "add" ? this.onAdd : this.onUpdate;
    let primaryInfo = null;
    if (this.state.modalDestination !== "add") {
      primaryInfo = this.state.tarifs.filter(
        (item) => item.idTarif === this.state.selectedItem
      );
      if (primaryInfo.length != 0) {
        primaryInfo = primaryInfo[0];
      } else {
        primaryInfo = null;
      }
    }
    let modal = this.state.modal ? (
      <Portal>
        <TarifModal
          {...primaryInfo}
          onToggle={this.onToggleModal}
          onAction={correct}
        ></TarifModal>
      </Portal>
    ) : null;

    let potentialError = this.state.error ? (
      <Error message={this.state.errorMessage}></Error>
    ) : null;

    let finallyTarifs = this.onFilter().map((item) => (
      <TarifListItem
        {...item}
        key={item.idTarif}
        forSwitch={this.#switchToUpdate}
        forToggle={this.onToggleModal}
        onDelete={this.onDelete}
      ></TarifListItem>
    ));
    let notification =
      finallyTarifs.length === 0 ? (
        <Label message="  "></Label>
      ) : null;
    return (
      <div className="d-flex flex-column mx-auto w-75 p-3 bg-white overflow-y-hidden">
        {modal}
        {tokenModal}
        {potentialError}
        <Search onInput={this.onInput}></Search>
        <div className="d-flex mb-2">
          <button
            onClick={(e) => {
              this.onToggleModal();
              this.#switchToAdd();
            }}
            className="border-0 bg-white text-black fs-4"
          >
            <i class="bi bi-patch-plus-fill"></i>
          </button>
          <Button
            className="ms-2"
            variant="warning"
            onClick={this.#toggleTokenModal}
          >
             
          </Button>
        </div>
        <div className="tarif__name-column d-flex justify-content-between bg-info p-3 align-items-center">
          <span
            style={{ width: "30%" }}
            className="w-30 text-white text-center"
          >
            
          </span>
          <span
            style={{ width: "30%" }}
            className="w-30 text-white text-center"
          >
            
          </span>
          <span
            style={{ width: "30%" }}
            className="w-30 text-white text-center"
          >
            
          </span>
          <span
            style={{ width: "50px" }}
            className="w-30 text-danger fs-4 text-center"
          >
            &#10008;
          </span>
        </div>
        {notification}
        <TarifList>{finallyTarifs}</TarifList>
      </div>
    );
  }
}
export default TarifSection;
