import React, { Component } from "react";
import SettingService from "../../../services/settingService";
import Error from "../../errorMessageComponent/errorMessageComponent";
import Load from "../../loadComponent/load";
import Referal from "../../referalPayHistory/referalPayHistory";
class GeneralData extends Component {
  #_settingService = new SettingService();
  state = {
    error: false,
    errorMessage: "",
    onLoad: false,
  };
  componentDidMount() {
    this.setState({ onLoad: true });
    this.#_settingService
      .getSetting()
      .then((e) => this.setState(e))
      .catch((e) => this.setState({ error: true, errorMessage: e.message }))
      .finally(() =>
        this.setState({
          onLoad: false,
        })
      );
  }
  render() {
    let loadComponent = this.state.onLoad ? (
      <Load message=" ..."></Load>
    ) : null;
    let error = null;
    if (this.state.error)
      error = <Error message={this.state.errorMessage}></Error>;
    let {
      frozenAmountRub,
      frozenAmountUsdt,
      frozenAmountTon,
      frozenAmountDel,
      withdrawalAmountRub,
      withdrawalAmountUsdt,
      withdrawalAmountTon,
      withdrawalAmountDel,
    } = this.state;
    return (
      <div className="d-flex w-75 m-2 bg-white flex-column p-3">
        {error}
        <div className="d-flex align-items-center">
          <h2 className="text-black fs-3 me-3 my-0"></h2>
          {loadComponent}
        </div>
        <div className="d-flex text-black mt-2 justify-content-between">
          <div className="d-flex flex-column parametres-column">
            <h3></h3>
            <span>RUB: {withdrawalAmountRub + frozenAmountRub} </span>
            <span>USDT: {withdrawalAmountUsdt + frozenAmountUsdt}</span>
            <span>TON: {withdrawalAmountTon + frozenAmountTon}</span>
            <span>DEL: {withdrawalAmountDel + frozenAmountDel}</span>
          </div>
          <div className="d-flex flex-column parametres-column">
            <h3>  </h3>
            <span>RUB: {withdrawalAmountRub}</span>
            <span>USDT: {withdrawalAmountUsdt}</span>
            <span>TON: {withdrawalAmountTon}</span>
            <span>DEL: {withdrawalAmountDel}</span>
          </div>
          <div className="d-flex flex-column parametres-column">
            <h3></h3>
            <span>RUB: {frozenAmountRub}</span>
            <span>USDT: {frozenAmountUsdt}</span>
            <span>TON: {frozenAmountTon}</span>
            <span>DEL: {frozenAmountDel}</span>
          </div>
        </div>
        <Referal></Referal>
      </div>
    );
  }
}
export default GeneralData;
