import PanelTabComponent from "./panelTabComponent/panelTabComponent";
import { Component } from "react";
import React from "react";
import { Button } from "react-bootstrap";
import UserComponent from "./userComponent/userComponent";
import GeneralData from "./generalData/generalData";
import SettingService from "../../services/settingService";
import TarifSection from "./tarifs/tarifComponent";
import AuthorizationPage from "../authorization-page/authorizationPage";
import ReferalSection from "../referalSection/referalSection";
import SettingsSection from "./settingComponent/Settings";
class PanelPage extends Component {
  state = { currentSection: "generalData" };
  defineCurrentSection() {
    switch (this.state.currentSection) {
      case "generalData":
        return <GeneralData></GeneralData>;
      case "users":
        return <UserComponent></UserComponent>;
      case "tarifs":
        return <TarifSection></TarifSection>;
      case "referals":
        return <ReferalSection></ReferalSection>;
      case "settings":
        return <SettingsSection></SettingsSection>;
      default:
        return <AuthorizationPage></AuthorizationPage>;
    }
  }
  #updateSection = (e) => {
    this.setState({ currentSection: e.target.getAttribute("data-attribute") });
    document
      .querySelectorAll("[data-attribute]")
      .forEach((item) => item.classList.remove("bg-info", "text-white"));
    e.target.classList.add("bg-info");
  };
  render() {
    let currentSection = this.defineCurrentSection();
    return (
      <div className="d-flex">
        <PanelTabComponent>
          <Button
            variant={
              this.state.currentSection == "generalData" ? "info" : "primary"
            }
            data-attribute="generalData"
            onClick={this.#updateSection}
          >
             
          </Button>
          <Button data-attribute="users" onClick={this.#updateSection}>
            
          </Button>
          <Button data-attribute="referals" onClick={this.#updateSection}>
            
          </Button>
          <Button data-attribute="tarifs" onClick={this.#updateSection}>
            
          </Button>
          <Button data-attribute="settings" onClick={this.#updateSection}>
            
          </Button>
        </PanelTabComponent>
        {currentSection}
      </div>
    );
  }
}
export default PanelPage;
