import React, { Component } from "react";
class PanelTabComponent extends Component {
  render() {
    return (
      <div
        className="bg-light d-flex flex-column justify-content-center mr-5"
        style={{ height: "100vh", width: "15%" }}
      >
        {React.Children.map(this.props.children, (children) => {
          return React.cloneElement(children, {
            className: "shadow-sm p-3 text-black rounded-0",
          });
        })}
      </div>
    );
  }
}
export default PanelTabComponent;
