import React, { Component } from "react";
import ReferalService from "../../services/referalPayHistoryService";
import Error from "../errorMessageComponent/errorMessageComponent";
import Load from "../loadComponent/load";
import "../panelPage/common.css";
class Referal extends Component {
  #_usdt = "usdt";
  #_rub = "rub";
  #_del = "del";
  #_ton = "ton";
  #_statusRef = "ref";
  #_statusNonRef = "non";
  #_refefalService = new ReferalService();
  state = { error: false, errorMessage: "", data: [], load: false };
  componentDidMount() {
    this.setState({ load: true });
    this.#_refefalService
      .getInfoAboutReferalPays()
      .then((e) => this.setState({ data: e }))
      .catch((e) => this.setState({ error: true, errorMessage: e.message }))
      .finally(() =>
        this.setState({
          load: false,
        })
      );
  }
  #distributePaysFor = (status) => {
    return this.state.data.filter((item) => item.status === status);
  };
  #countTheSumOfCost = (array, currency) => {
    return array.reduce((accum, item) => {
      if (item.currency === currency.toUpperCase()) return accum + item.cost;
      return accum;
    }, 0);
  };
  render() {
    let errorMessage = this.state.errorMessage;
    let potentialError = this.state.error ? (
      <Error message={errorMessage}></Error>
    ) : null;
    let load = this.state.load ? (
      <Load message="  ..."></Load>
    ) : null;
    let refs = this.#distributePaysFor(this.#_statusRef);
    let nonRefs = this.#distributePaysFor(this.#_statusNonRef);
    return (
      <div className="d-flex flex-column mt-5">
        {potentialError}
        {load}
        <div className="d-flex justify-content-between mt-3">
          <div className="d-flex flex-column parametres-column">
            <div className="d-flex align-items-center">
              <h2 className="text-black fs-3 me-3 my-0"> </h2>
            </div>
            <span>
              RUB: {this.#countTheSumOfCost(this.state.data, this.#_rub)}
            </span>
            <span>
              USDT:{this.#countTheSumOfCost(this.state.data, this.#_usdt)}
            </span>
            <span>
              TON:{this.#countTheSumOfCost(this.state.data, this.#_ton)}
            </span>
            <span>
              DEL:{this.#countTheSumOfCost(this.state.data, this.#_del)}
            </span>
          </div>
          <div className="d-flex flex-column parametres-column">
            <h3>,  </h3>
            <span>RUB: {this.#countTheSumOfCost(refs, this.#_rub)}</span>
            <span>USDT: {this.#countTheSumOfCost(refs, this.#_usdt)}</span>
            <span>TON: {this.#countTheSumOfCost(refs, this.#_ton)}</span>
            <span>DEL: {this.#countTheSumOfCost(refs, this.#_del)}</span>
          </div>
          <div className="d-flex flex-column parametres-column">
            <h3> </h3>
            <span>RUB: {this.#countTheSumOfCost(nonRefs, this.#_rub)}</span>
            <span>USDT: {this.#countTheSumOfCost(nonRefs, this.#_usdt)}</span>
            <span>TON: {this.#countTheSumOfCost(nonRefs, this.#_ton)}</span>
            <span>DEL:{this.#countTheSumOfCost(nonRefs, this.#_del)}</span>
          </div>
        </div>
      </div>
    );
  }
}
export default Referal;
