import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import React, { Component } from "react";
import DataService from "../../services/dataServices";
import { getResourceWithoutTransform } from "../../services/dataServices";
class DataModal extends Component {
  #dataService = new DataService();
  state = { firstDate: "", secondDate: "", generated: false };
  #onSubmit = (e) => {
    e.preventDefault();
    this.props.onLoad();
    this.#dataService
      .getUserData(this.state.firstDate, this.state.secondDate)
      .then(() => {
        this.props.onLoad();
        this.setState({ generated: true });
      })
      .catch(() => this.props.onError("   "));
  };
  #onInput = (e) => {
    this.setState({ [e.target.getAttribute("data-for")]: e.target.value });
  };
  render() {
    return (
      <div
        onClick={(e) => {
          if (e.target.classList.contains("close")) this.props.onExit();
        }}
        style={{ height: "100vh", backgroundColor: "rgba(0,0,0,.5)" }}
        className="close d-flex position-absolute  top-0 left-0 w-100 align-items-center justify-content-center"
      >
        <Form onSubmit={this.#onSubmit} className="bg-light text-black p-3">
          <Form.Group className="d-flex justify-content-between">
            <Form.Group className="mb-3">
              <Form.Label> </Form.Label>
              <Form.Control
                onChange={this.#onInput}
                type="date"
                placeholder=" "
                data-for="firstDate"
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label> </Form.Label>
              <Form.Control
                onChange={this.#onInput}
                type="date"
                placeholder=" "
                data-for="secondDate"
              />
            </Form.Group>
          </Form.Group>
          <Button className="me-2" variant="success" type="submit">
             
          </Button>
        </Form>
      </div>
    );
  }
}

export default DataModal;
