import React, { Component } from "react";
import SettingService from "../../../services/settingService";
import { Button } from "react-bootstrap";
import Error from "../../errorMessageComponent/errorMessageComponent";
import MailModal from "../../mailModal/mailModal";
import Portal from "../../portalComponent/portalComponent";
import { FloatingLabel } from "react-bootstrap";
import Load from "../../loadComponent/load";
class SettingsSection extends Component {
  state = {
    usdtProc: "",
    tonProc: "",
    delProc: "",
    referalProc1lvl: "",
    referalProc2lvl: "",
    referalProc3lvl: "",
    error: false,
    errorMessage: "",
    buttonEnabled: false,
    modal: false,
    onLoad: false,
  };
  onToggleModal = () => {
    this.setState((state) => ({ modal: !state.modal }));
  };
  #settingService = new SettingService();
  #onSubmit = (e) => {
    e.preventDefault();
    this.setState({ onLoad: true });
    let {
      usdtProc,
      tonProc,
      delProc,
      referalProc1lvl,
      referalProc2lvl,
      referalProc3lvl,
    } = this.state;
    this.#settingService
      .updateSetting({
        usdtProc,
        tonProc,
        delProc,
        referalProc1lvl,
        referalProc2lvl,
        referalProc3lvl,
      })
      .then((e) => this.setState({ error: false }))
      .catch((e) => {
        this.setState({ error: true, errorMessage: e.message });
      })
      .finally(() => this.setState({ buttonEnabled: false, onLoad: false }));
  };
  componentDidMount() {
    this.setState({ onLoad: true });
    this.#settingService
      .getSetting()
      .then((e) => {
        this.setState({ ...e });
      })
      .catch((e) => {
        this.setState({
          error: true,
          errorMessage: e.message,
        });
      })
      .finally(() => this.setState({ onLoad: false }));
  }
  #onInput = (e) => {
    this.setState({
      buttonEnabled: true,
      [e.target.getAttribute("data-for")]: e.target.value,
    });
  };
  render() {
    let loadComponent = this.state.onLoad ? (
      <Load message=" .."></Load>
    ) : null;
    let mailModal = this.state.modal ? (
      <Portal>
        <MailModal onToggle={this.onToggleModal}></MailModal>
      </Portal>
    ) : null;
    let potentialError = this.state.error ? (
      <Error message={this.state.errorMessage}></Error>
    ) : null;
    let {
      usdtProc,
      tonProc,
      delProc,
      referalProc1lvl,
      referalProc2lvl,
      referalProc3lvl,
    } = this.state;

    return (
      <div className="d-flex p-3 flex-column">
        {mailModal}
        <div className="d-flex flex-column procent-settings">
          <h2> </h2>
          {potentialError}
          <form onSubmit={this.#onSubmit} action="#">
            <div className="mt-3 fs-5">
              <span className="fs-4">USDT </span>
              <input
                type="text"
                data-for="usdtProc"
                value={usdtProc}
                onChange={this.#onInput}
                className="ms-4"
              />
            </div>
            <div className="mt-3 fs-5">
              <span className="fs-4">TON </span>
              <input
                type="text"
                data-for="tonProc"
                value={tonProc}
                onChange={this.#onInput}
                className="ms-4"
              />
            </div>
            <div className="mt-3 fs-5">
              <span className="fs-4">DEL </span>
              <input
                type="text"
                data-for="delProc"
                value={delProc}
                onChange={this.#onInput}
                className="ms-4"
              />
            </div>
            <div className="mt-3 fs-5">
              <span className="fs-4">  1 </span>
              <input
                type="text"
                data-for="referalProc1lvl"
                value={referalProc1lvl}
                onChange={this.#onInput}
                className="ms-4"
              />
            </div>
            <div className="mt-3 fs-5">
              <span className="fs-4">  2 </span>
              <input
                type="text"
                data-for="referalProc2lvl"
                value={referalProc2lvl}
                onChange={this.#onInput}
                className="ms-4"
              />
            </div>
            <div className="mt-3 fs-5">
              <span className="fs-4">  3 </span>
              <input
                type="text"
                data-for="referalProc3lvl"
                value={referalProc3lvl}
                onChange={this.#onInput}
                className="ms-4"
              />
            </div>
            <div className="d-flex mt-3">
              <Button
                className=" me-3"
                type="submit"
                disabled={!this.state.buttonEnabled}
              >
                
              </Button>
              {loadComponent}
            </div>
          </form>
        </div>
        <Button
          onClick={this.onToggleModal}
          variant="primary"
          className="mt-2 w-50"
        >
           
        </Button>
      </div>
    );
  }
}
export default SettingsSection;
