import React, { Component } from "react";
import Error from "../errorMessageComponent/errorMessageComponent";
import { FloatingLabel } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import MessageService from "../../services/messageService";
import Load from "../loadComponent/load";
class MailModal extends Component {
  state = {
    currentMessage: "",
    forActive: false,
    onLoad: false,
    error: false,
    errorMessage: "",
  };
  #messageService = new MessageService();
  #onInput = (e) => {
    this.setState({ [e.target.getAttribute("data-for")]: e.target.value });
  };
  #onChecked = (e) => {
    let value = e.target.checked;
    this.setState({ forActive: value });
  };
  #onSubmit = (e) => {
    e.preventDefault();
    this.setState({ onLoad: true });
    this.#messageService
      .addMessage({
        text: this.state.currentMessage,
        type: this.state.forActive ? 1 : 0,
      })
      .then(() => {
        this.setState({ onLoad: false });
      })
      .catch((e) => this.setState({ error: true, errorMessage: e.message }))
      .finally(this.setState({ currentMessage: "", forActive: false }));
  };
  render() {
    let loadComponent = this.state.onLoad ? (
      <Load message="   .."></Load>
    ) : null;
    let potentialError = this.state.error ? (
      <Error message={this.state.errorMessage}></Error>
    ) : null;
    return (
      <div
        onClick={(e) => {
          if (e.target.classList.contains("close")) this.props.onToggle();
        }}
        style={{ height: "100vh", backgroundColor: "rgba(0,0,0,.5)" }}
        className="close d-flex position-absolute  top-0 left-0 w-100 align-items-center justify-content-center flex-column"
      >
        {potentialError}
        <Form
          className="p-3 bg-light text-black w-50"
          onSubmit={this.#onSubmit}
        >
          <Form.Group className="mb-3">
            <FloatingLabel controlId="floatingTextarea2" label="">
              <Form.Control
                data-for="currentMessage"
                as="textarea"
                value={this.state.currentMessage}
                placeholder="..."
                style={{ height: "350px", resize: "none" }}
                onChange={this.#onInput}
              />
            </FloatingLabel>
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Check
              type="checkbox"
              label="  "
              onChange={this.#onChecked}
              checked={this.state.forActive}
            />
          </Form.Group>
          <div className="d-flex">
            <Button
              className="me-3"
              variant="primary"
              type="submit"
              disabled={this.setState.onLoad}
            >
                
            </Button>
            {loadComponent}
          </div>
        </Form>
      </div>
    );
  }
}

export default MailModal;
