import React, { Component } from "react";
import { UserService } from "../../../../services/apiServices";
import Error from "../../../errorMessageComponent/errorMessageComponent";
class UserListItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isRef: props.isRef ? 1 : 0,
      isActive: props.isActive ? 1 : 0,
      connectionError: false,
      errorMessage: "",
    };
  }
  #userService = new UserService();
  #onUpdate = async (e) => {
    let result = e.target.checked ? 1 : 0;
    this.setState({
      [e.target.getAttribute("data-context")]: result,
    });
  };
  componentDidUpdate(prevProp, prevState) {
    if (prevState != this.state)
      this.#userService
        .updateUser(
          { isRef: this.state.isRef, isActive: this.state.isActive },
          this.props.idUser
        )
        .catch((e) =>
          this.setState({ connectionError: true, errorMessage: e.message })
        );
  }
  render() {
    if (this.state.connectionError)
      return <Error message={this.state.errorMessage}></Error>;
    let {
      username,
      payUsdt,
      payTon,
      payDel,
      payRub,
      statusTarif,
      datePayTarif,
      dateReg,
      disableInput,
      onUserClick,
    } = this.props;
    return (
      <li
        onClick={onUserClick ? (e) => onUserClick(username) : null}
        className="font-size bg-light d-flex border border-primary justify-content-between w-100 p-2"
      >
        <span className="user__name w-9">{username ? username : "None"}</span>
        <span className="user__pay-usdt w-9">{payUsdt ? payUsdt : "None"}</span>
        <span className="user__pay-ton w-9">{payTon ? payTon : "None"}</span>
        <span className="user__pay-del w-9">{payDel ? payDel : "None"}</span>
        <span className="user__pay-rub w-9">{payRub ? payRub : "None"}</span>
        <input
          onChange={this.#onUpdate}
          type="checkbox"
          data-context="isRef"
          disabled={disableInput}
          checked={this.state.isRef}
          className="name__is-ref w-9"
        />
        <input
          onChange={this.#onUpdate}
          type="checkbox"
          data-context="isActive"
          disabled={disableInput}
          checked={this.state.isActive}
          className="user__is-active w-9"
        />
        <span className="user__tarif-status w-9">
          {statusTarif ? statusTarif : "None"}
        </span>
        <span className="user__pay-date w-9">
          {datePayTarif ? datePayTarif : "None"}
        </span>
        <span className="user__date-reg w-9">{dateReg ? dateReg : "None"}</span>
        {/*  <button
            onClick={(e) => this.props.onUserDelete(this.props.idUser)}
            className="user__delete"
          >
            <i class="bi bi-trash"></i>
          </button> */}
      </li>
    );
  }
}
export default UserListItem;
