import { mustHaveHeader } from "./apiServices";
async function addResourceWithoutReceive(addResource, url) {
  let res = await fetch(url, {
    method: "POST",
    headers: { ...mustHaveHeader, "Content-type": "application/json" },
    body: JSON.stringify(addResource),
  });
  if (!res.ok) throw new Error("  " + res.status);
  return res;
}

class MessageService {
  #_apiBase = "http://185.185.70.166:5195/api/message";
  async addMessage(message) {
    return await addResourceWithoutReceive(message, this.#_apiBase);
  }
}
export default MessageService;
