import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import React, { Component } from "react";
import TokenList from "./tokenList";
import TokenService from "../../services/tokenService";
import TokenListItem from "./tokenListItem";
import Error from "../errorMessageComponent/errorMessageComponent";
import Load from "../loadComponent/load";
import Label from "../errorMessageComponent/notFound";
class TokenModal extends Component {
  state = {
    currentPromo: [],
    error: false,
    errorMessage: "",
    currentTarifName:
      this.props.tarifs.length > 0 ? this.props.tarifs[0].nameT : "",
    onLoadTokens: false,
    onGenerate: false,
  };
  #tokenService = new TokenService();
  componentDidMount() {
    this.setState({ onLoadTokens: true });
    this.#tokenService
      .getTokens()
      .then((e) => this.setState({ currentPromo: e }))
      .catch((e) => this.setState({ error: true, errorMessage: e.message }))
      .finally(() => this.setState({ onLoadTokens: false }));
  }
  #onInput = (e) => {
    this.setState({ [e.target.getAttribute("data-for")]: e.target.value });
  };
  #onDelete = (id) => {
    this.#tokenService
      .deleteToken(id)
      .then(() =>
        this.setState((state) => {
          let result = state.currentPromo.filter((item) => item.idToken !== id);
          return { currentPromo: result };
        })
      )
      .catch((e) => this.setState({ error: true, errorMessage: e.message }));
  };
  #onGenerate = (e) => {
    e.preventDefault();
    this.setState({ onGenerate: true });
    this.#tokenService
      .addToken({ tarifName: this.state.currentTarifName })
      .then((e) => {
        this.setState((state) => {
          let result = [e, ...state.currentPromo];
          return { currentPromo: result, error: false };
        });
      })
      .catch((e) => this.setState({ error: true, errorMessage: e.message }))
      .finally(() => this.setState({ onGenerate: false }));
  };
  render() {
    let generateToken = this.state.onGenerate ? (
      <Load message=" ..."></Load>
    ) : null;
    let loadTokens = this.state.onLoadTokens ? (
      <Label message=" ..."></Label>
    ) : null;
    let tokens = this.state.currentPromo.map((item) => (
      <TokenListItem
        {...item}
        onDelete={this.#onDelete}
        key={item.idToken}
      ></TokenListItem>
    ));
    let potentialError = this.state.connectionError ? (
      <Error message={this.state.errorMessage}></Error>
    ) : null;
    let options = this.props.tarifs.map((item) => (
      <option key={item.idTarif}>{item.nameT}</option>
    ));
    return (
      <div
        onClick={(e) => {
          if (e.target.classList.contains("close")) this.props.onExit(e);
        }}
        style={{ height: "100vh", backgroundColor: "rgba(0,0,0,.5)" }}
        className="close d-flex position-absolute  top-0 left-0 w-100 align-items-center justify-content-center"
      >
        <div className="d-flex w-75 flex-column bg-light p-5">
          {potentialError}
          <Form onSubmit={this.#onGenerate} className="w-30">
            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Select
                onChange={this.#onInput}
                size="lg"
                data-for="currentTarifName"
              >
                {options}
              </Form.Select>
            </Form.Group>
            <div className="d-flex align-items-center mt-2">
              <Button variant="success" type="submit" className="me-3">
                 
              </Button>
              {generateToken}
            </div>
          </Form>
          <h3 className="mt-2 text-center"></h3>
          <div className="d-flex justify-content-between bg-info p-3 mt-2">
            <span style={{ width: "40%" }} className="text-center">
               
            </span>
            <span style={{ width: "40%" }} className="text-center">
              
            </span>
            <span
              style={{ width: "50px" }}
              className="w-30 text-danger fs-4 text-center"
            >
              &#10008;
            </span>
          </div>
          <TokenList>
            {tokens}
            {loadTokens}
          </TokenList>
        </div>
      </div>
    );
  }
}

export default TokenModal;
