import "./App.css";
import React from "react";
import { Component } from "react";
import AuthorizationPage from "./components/authorization-page/authorizationPage";
import PanelPage from "./components/panelPage/panelPage";
import Auth from "./services/authServices";
class App extends Component {
  state = { currentPage: "auth" };
  #defineCurrentPage = () => {
    switch (this.state.currentPage) {
      case "auth":
        return (
          <AuthorizationPage onEntered={this.onEntered}></AuthorizationPage>
        );
      case "panelPage":
        return <PanelPage></PanelPage>;
    }
  };
  onEntered = (e) => {
    setTimeout(() => this.setState({ currentPage: "panelPage" }));
  };
  #authService = new Auth();
  componentDidMount() {
    this.#authService
      .CheckAuth()
      .then(() => this.setState({ currentPage: "panelPage" }))
      .catch(() => this.setState({ currentPage: "auth" }));
  }
  render() {
    return <>{this.#defineCurrentPage()}</>;
  }
}

export default App;
