import { Component } from "react";
import { AuthForm as MainForm } from "../authFormComponent/authFormComponent";
import { Container, Row, Col } from "react-bootstrap";
import React from "react";
import Error from "../errorMessageComponent/errorMessageComponent";
class AuthorizationPage extends Component {
  state = { error: false, errorMessage: "" };
  onFail = (message) => {
    this.setState({ error: true, errorMessage: message });
  };
  render() {
    let potentialError = this.state.error ? (
      <Error message={this.state.errorMessage}></Error>
    ) : null;
    return (
      <Container
        className="d-flex align-items-center justify-content-center"
        style={{ height: "100vh" }}
      >
        <Row>
          <Col>
            {potentialError}
            <MainForm
              onEntered={this.props.onEntered}
              onFail={this.onFail}
            ></MainForm>
          </Col>
        </Row>
      </Container>
    );
  }
}
export default AuthorizationPage;
