import { getResource, addResource } from "./apiServices";
class Auth {
  #_apiBase = "http://185.185.70.166:5195/api/auth";
  async GetAuth(username, password) {
    return await addResource({ username, password }, `${this.#_apiBase}`);
  }
  async CheckAuth() {
    let res = await fetch(this.#_apiBase, {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    });
    if (res.ok) return res;
    throw new Error(res.statusText);
  }
}
export default Auth;
