import React, { Component } from "react";
import "./tarifListItem.css";
const TarifListItem = (props) => {
  let {
    nameT,
    duration,
    count,
    onDelete,
    forToggle,
    forSwitch,
    idTarif,
  } = props;
  return (
    <li
      onClick={(e) => {
        if (e.target.nodeName !== "BUTTON" && e.target.nodeName !== "I") {
          forSwitch(idTarif);
          forToggle();
        }
      }}
      className="bg-light text-black d-flex justify-content-around p-2 border border-primary align-items-center"
    >
      <span style={{ width: "30%" }} className="tarif__name w-30">
        {nameT}
      </span>
      <span style={{ width: "30%" }} className="tarif__duration w-30">
        {duration}
      </span>
      <span style={{ width: "30%" }} className="tarif__cost w-30">
        {count}
      </span>
      <button
        onClick={(e) => onDelete(idTarif)}
        className="delete border-0 text-danger fs-5"
      >
        <i class="bi bi-trash"></i>
      </button>
    </li>
  );
};
export default TarifListItem;
