import { Component } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import React from "react";
import Auth from "../../services/authServices";
import { redefineToken } from "../../services/apiServices";
class AuthForm extends Component {
  state = {
    login: "",
    password: "",
  };
  #authService = new Auth();
  #onInput = (e) => {
    this.setState({ [e.target.getAttribute("data-for")]: e.target.value });
  };
  #onEnter = (e) => {
    e.preventDefault();
    this.#authService
      .GetAuth(this.state.login, this.state.password)
      .then((tokenJson) => {
        this.props.onEntered();
        localStorage.setItem("token", tokenJson.token);
        redefineToken();
      })
      .catch((e) => this.props.onFail(e.message));
  };
  render() {
    return (
      <Form
        onSubmit={this.#onEnter}
        className="bg-dark rounded p-5 text-white rounded-information border border-primary"
      >
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label></Form.Label>
          <Form.Control
            type="text"
            placeholder=" "
            data-for="login"
            onChange={this.#onInput}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label></Form.Label>
          <Form.Control
            type="password"
            placeholder=" "
            data-for="password"
            onChange={this.#onInput}
          />
        </Form.Group>
        <Button variant="primary" type="submit">
          
        </Button>
      </Form>
    );
  }
}
export { AuthForm };
