import { getResource, mustHaveHeader } from "./apiServices";
async function getResourceWithoutTransform() {
  let res = await fetch("http://185.185.70.166:5195/secret/excel.xlsx", {
    method: "GET",
    headers: { ...mustHaveHeader },
  });
  if (!res.ok) throw new Error(res.statusText + res.status);
  let blob = await res.blob();
  let url = URL.createObjectURL(blob);

  let a = document.createElement("a");
  a.href = url;
  a.download = "file.xlsx";
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
  URL.revokeObjectURL(url);
}
export { getResourceWithoutTransform };
class DataService {
  #_apiBase = "http://185.185.70.166:5195/api/data";

  async getUserData(firstPoint, lastPoint) {
    let dto = { firstPoint, lastPoint };
    let res = await fetch(`${this.#_apiBase}/user`, {
      method: "POST",
      headers: {
        ...mustHaveHeader,
        "Content-type": "application/json",
        Accept: "*",
      },
      body: JSON.stringify(dto),
    });
    if (!res.ok) throw new Error("     ");
    let blob = await res.blob();
    let url = URL.createObjectURL(blob);

    let a = document.createElement("a");
    a.href = url;
    a.download = "file.xlsx";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }
}
export default DataService;
