import React, { Component } from "react";
import Search from "../searchComponent/searchComponent";
import UserList from "../panelPage/userComponent/usersListComponent/usersList";
import Error from "../errorMessageComponent/errorMessageComponent";
import ReferalService from "../../services/referalService";
import UserListItem from "../panelPage/userComponent/userListItem/userListItem";
import { Button } from "react-bootstrap";
import Label from "../errorMessageComponent/notFound";
class ReferalSection extends Component {
  state = {
    previousUser: "",
    currentUser: "None",
    error: false,
    errorMessage: "",
    currentReferals: [],
    canBack: false,
  };
  #referalService = new ReferalService();
  componentDidUpdate(prevProps, prevState) {
    if (
      prevState.currentUser !== this.state.currentUser &&
      this.state.currentUser != ""
    ) {
      this.#referalService
        .getReferals(this.state.currentUser)
        .then((userArray) =>
          this.setState({
            currentReferals: userArray,
            error: false,
          })
        )
        .catch((e) =>
          this.setState({
            error: true,
            errorMessage: e.message,
          })
        );
    }
  }
  #onUserClick = (username) => {
    this.setState((state) => ({
      previousUser: state.currentUser,
      currentUser: username,
      canBack: true,
    }));
  };
  switchToPreviousGuy = () => {
    let temp = this.state.currentUser;
    this.setState((state) => ({
      currentUser: state.previousUser,
      previousUser: temp,
    }));
  };
  #changeKeyWord = (value) => {
    this.setState((state) => ({
      previousUser: state.currentUser,
      currentUser: value,
    }));
  };
  render() {
    let notification =
      this.state.currentReferals.length === 0 &&
      this.state.currentUser !== "None" ? (
        <Label message="  "></Label>
      ) : null;
    let users = this.state.currentReferals.map((item) => (
      <UserListItem
        onUserClick={this.#onUserClick}
        {...item}
        disableInput={true}
        key={item.idUser}
      ></UserListItem>
    ));
    let errorMessage = this.state.errorMessage;
    let potentialError = this.state.error ? (
      <Error message={errorMessage}></Error>
    ) : null;
    let backButton = this.state.canBack ? (
      <Button
        style={{ display: "flex" }}
        onClick={this.switchToPreviousGuy}
        variant="link"
      >
        
      </Button>
    ) : null;
    return (
      <div className="d-flex flex-column p-3 w-75 mx-auto">
        {potentialError}
        <Search onInput={this.#changeKeyWord}></Search>
        <div
          style={{ minHeight: "35px" }}
          className="d-flex w-100 position-relative justify-content-between align-items-center"
        >
          {backButton}
          <span style={{ top: "-25px" }} className="position-absolute start-0">
               {this.state.currentUser}
          </span>
        </div>
        <div className="d-flex justify-content-between mt-2 bg-info p-3">
          <span className="w-9 text-white"></span>
          <span className="w-9 text-white">pay_usdt</span>
          <span className="w-9 text-white">pay_ton</span>
          <span className="w-9 text-white">pay_del</span>
          <span className="w-9 text-white">pay_rub</span>
          <span className="w-9 text-white"></span>
          <span className="w-9 text-white"></span>
          <span className="w-9 text-white"> </span>
          <span className="w-9 text-white">DPT</span>
          <span className="w-9 text-white">Registation</span>
        </div>
        {notification}
        <UserList>{users}</UserList>
      </div>
    );
  }
}
export default ReferalSection;
