import {
  getResource,
  deleteResource,
  updateResource,
  addResource,
} from "./apiServices";
class TarifService {
  #_apiBase = "http://185.185.70.166:5195/api/tarif";
  async addTarif(AddTarifDto) {
    return await addResource(AddTarifDto, this.#_apiBase);
  }
  async updateTarif(UpdateTarifDto, id) {
    return await updateResource(UpdateTarifDto, `${this.#_apiBase}/${id}`);
  }
  async getTarif(id) {
    return await getResource(`${this.#_apiBase}/${id}`);
  }
  async getTarifs() {
    return await getResource(this.#_apiBase);
  }
  async deleteTarif(id) {
    return await deleteResource(`${this.#_apiBase}/${id}`);
  }
}
export default TarifService;
